.nav-mini {
    height: 25px !important;
    top: 0;
    background: var(--grijs );
    .aftertekst {
        font-size: .5em;
        background: rgba(0, 0, 0, 0.25);
        color: rgba(255, 255, 255, 0.9);
        padding: .75em;
        border-radius: 2.5px;
        margin-left: 10px;
        display: flex;
        left: 10vw;
    }
    nav a {font-size: .7em!important}
    nav a:hover {border-bottom: 2px solid transparent;transform: none;color: var(--sec)!important;}
}

.kleinenav {
    display: flex!important;
    height: 45px!important;
       opacity: 1!important;
}

.naam {
    font-size: 1em!important;
    opacity: 0;
    position: fixed;
    top: 13px;
}

.nav {
    z-index: 999;
    height: 100px;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1em 0;
    position: fixed;
    transition: .5s ease;
    transition-property: height, opacity;
    img {
        height: 100px;
        opacity: 0;
        transition: .5s ease;
        transition-property: height, opacity;
        position: fixed;
        top: 15px;
        left: 10vw;
    }
    nav a {
        border-bottom: 2px solid transparent;
        transition: all .25s ease;
        &:hover {
            transform: translateY(-10px);
            border-bottom: 2px solid var(--sec);
        }
    }
}

.nav a, .nav-mini a {
    text-decoration: none;
    color: white;
    font-weight: 900;
    font-size: .7em;
    padding: 1em;
    display: inline-flex;
}

.socials, .nav, .nav-mini {display: flex;opacity: 0;z-index: 999}
.socials a {
    font-size: 1.25em;
    i {
        transition: all .25s ease;
        &:hover {transform: scale(1.25);color: var(--sec);}
    
    }
}

.socials {
    position: absolute;
    right: 10vw;
}

.hamburg {
    position: absolute;
    top: 19px;
    left: 15px;
    width: 20px;
    height: auto;
    display: flex;
    flex-direction: column;
    opacity: 0;
    transition: opacity .25s ease;
    cursor: pointer;
    &:hover {
        opacity: 1!important;
    }
}

.streep {
    background: white;
    width: 100%;
    height: 4px;
    padding: 0;
    margin: 0 0 5px 0;
    border-radius: 0;
}