@media only screen and (max-width: 900px) {
    .socials {display: none;}
    .gallerij_fotos {column-count: 2;}
    .gallerij_fotos img {opacity: 1;}
    .sectie {opacity: 1;}
}

@media only screen and (max-height: 700px) {
    .knopjes {
        right: 10vw;
        left: unset;
    }
}

@media only screen and (max-width: 650px) {
    .policy span {padding-left: 2em;}
    .policy i {margin: 0; margin-left: 1em;}
    .approved {margin-left: 1em;}
    .gallerij_fotos {padding: 2em 0 1em 0;}
    #about {margin-bottom: 0;}
    .preview {grid-template-columns: repeat(1, 1fr);}
    #filter button {width: 50%;}
    .loading_tekst span:nth-child(2) {margin-left: 0;}
    .info {
        align-items: center;
        left: 0vw;
        width: 100vw;
        a {display: none;}
        h1 {
            font-size: 1em;
            margin: 3em 0;
            span {
                font-size: 4em;
                margin-top: -25px;
                margin-left: 0px;
            }
        }
        div {
            font-size: .9em;
            width: 275px;
            margin: 2em 0;
            line-height: 25px;
        }
    }

    .knopjes {display: none;}
    .figure {left: 0;width: 100%;opacity: .5;}

    #about p, #about a {grid-column: 2 / 4;margin: 3em 0;}

    .loading {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
    }

    .loading_tekst {
        margin: 2em;
        display: flex;
        flex-direction: column;
    }

    .quotes {display: none}
    .scrolldown {right: 2vw;width: 7vw;}
    nav {
        font-size: 1em;
        position: absolute;
        top: 8em;
    }

    .nav {img {position: initial;height: 80px;}}
    .nav-mini {
        .hamburg {opacity: .25;}
        nav {
            font-size: 1em;
            top: 55px;
            position: absolute;
            left: 0;
            width: 100%;
            height: 0px;
            opacity: 0;
            display: flex;
            background: var(--grijs);
            animation: weggaan .1 ease;
            transition: all .25s ease;
        }
    }

    @keyframes weggaan {
        0% {opacity: .25;}
        50% {opacity: 0;}
        100% {opacity: .25;}
    }
    .naam {
        margin: 0;
        position: absolute;
        left: 40px;
    }

    .sectie {
        &:nth-child(1) {grid-row: 1; grid-column: 1 / 3;}
        &:nth-child(2) {grid-row: 2; grid-column: 1 / 3;}
    }
}

@media only screen and (max-height: 650px) {
    .info div p {
        line-height: 1.5em;
    }
}
