.loading {
    z-index: -1;
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 33vw 33vw 33vw;
    color: white;
    justify-items: center;
    align-items: center;
    font-family: 'Space Mono', monospace;
    img {opacity: 0;width: 200px;height: 200px;}
}

.quotes {
    opacity: 0;
    width: 33vw;
    margin: 5em;
    line-height: 25px;
    p:before {
        content: open-quote;
        font-size: 10em;
        font-weight: bolder;
        opacity: .1;
        position: absolute;
        margin-left: -75px;
    }
    p:after {
        content: close-quote;
        font-size: 10em;
        font-weight: bolder;
        opacity: .1;
        position: absolute;
        margin-top: 50px;
    }
    span {color:var(--sec);}
}

.loading_tekst {
    grid-column: 3 / 4;
    cursor: crosshair;
    margin: 0;
    font-size: 1em;
    font-weight: 600;
    text-transform: uppercase;
    text-align: center;
    color: #eee;
    animation: flickerAnimation 2s infinite;
    span:nth-child(1)::before {
        position: absolute;
        content: " ";
        width: 10%;
        height: 5px;
        top:0px;
        left: 0px;
        background: var(--sec);
        animation: loadbar 4s;
        animation-fill-mode: forwards;
    }
    span:nth-child(2) {
        margin-left: 10px;
        color:rgba(255,255,255,.25);
        opacity: 0;
        transition: opacity 1s ease;
    }
    span:nth-child(3) {
        margin-left: 10px;
        color: var(--sec)!important;
        opacity: 0;
        transition: opacity 1s ease;
    }
}

@keyframes loadbar {
    0%   { width:0%; }
    50%  { width:40%; }
    100% { width:100%; }
}

@keyframes flickerAnimation {
      0%   { opacity:1; }
      50%  { opacity:0.25; }
      100% { opacity:1; }
}

.donkerbg {
    position: fixed;
    background: #000;
    width: 100vw;
    height: 100vh;
    z-index: -1;
}