@import 'components/_variables';
@import 'components/_default';
@import 'components/_loading';
@import 'components/_nav';

.policy {
    padding: 1.5em 0;
    font-size: .75em;
    background: var(--sec);
    color:white;
    width: 100vw;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    z-index: 999;
    justify-content: center;
    align-items: center;
    a {color: var(--grijs); font-weight: 700; padding: 0 .5em;}
    i {font-size: 2em; margin: 0 2em; cursor: pointer;transition: transform .25s ease;}
    i:hover {transform: rotate(90deg);}
    .approved {
        font-family: "Open Sans", sans-serif;
        margin-left: 2em;
        padding: .5em;
        background: white;
        color: var(--sec);
        font-size: 1em;
        opacity: 0;
        transition: opacity .25s ease;
    }
}

.reveal {opacity: 1!important;}

.credit {
    padding: 1.5em 0;
    color: rgba(255, 255, 255, 0.33);
}

.footercredit_name {color:var(--sec)}

.content {
    opacity: 0;
    width: 100vw;
    display: none;
    left: 0;
    top: 0;
    z-index: 2;
    grid-template-rows: repeat(4, auto);
}

.content2 {
    width: 100%;
    grid-template-rows: 100px auto auto;
    grid-template-columns: auto;
}

.on {opacity: 1!important}
.off {opacity: 0!important}
.displaynone {display: none!important;}
.displaygrid {display: grid!important;}

#home {height: 800px;grid-row: 1;}

.figure {
    width: 60%;
    position: absolute;
    right: 0;
    top: 20vh;
    z-index: 9;
}

.info {
    color: white;
    width: 400px;
    left: 10vw;
    position: absolute;
    top: 180px;
    display: flex;
    flex-direction: column;
    div p {
        opacity: .7;
        margin:0 0 1.5em 0;
        line-height: 2em;
    }
    h1 {
        font-size: 1em;
        display: flex;
        flex-direction: column;
        color: var(--sec);
        span {
            font-size: 5.5em;
            margin-top: -40px;
            margin-left: -5px;
            color: white!important;
        }
    }
    a {
        color: white;
        font-weight: 800;
        text-decoration: none;
        transition: color .25s ease;
        &:hover {color: var(--sec);}
    }
}

.descriptie {margin-top: 2em;}

.knopjes {
    position: absolute;
    bottom: 10vh;
    left: 10vw;
    font-weight: 800;
    z-index: 99;
    text-decoration: none;
    font-family: 'Space Mono', monospace;
    a {
        color: white;
        text-decoration: none;
        border-bottom: 4px solid #242a30;
        padding: 1em 2em;
        margin-right: 2em;
        transition: all .25s ease;
        &:hover {
            border-bottom: 4px solid var(--sec);
            opacity: 1;
        }
    }
}

.scrolldown {
    position: fixed;
    bottom: 10vh;
    right: 5vw;
    z-index: 99;
    width: 3vw;
    animation: bounce 4s infinite cubic-bezier(.79,.14,.15,.86);
}

@keyframes bounce {
      0%, 100% {
        transform: translateY(0);
      }
      50% {
        transform: translateY(-20px);
      }
}

#contact {
    height: 80vh;
    align-content: center;
    grid-row: 4;
    grid-template-columns: 20vw auto 20vw;
    display: grid;
}

.contact_content {grid-column: 2;}
.contact_content h2, #work h2 {
    color: white;
    border-left: 5px solid var(--sec);
    padding-left: 20px;
}

.formpje {
    width: 100%;
    height: 420px;
    form {
        display: grid;
        grid-template-columns: auto auto;
        grid-template-rows: auto, auto, auto;
        color: white;
        width: auto;
        position: relative;
        label {
            margin: 2em 0 1em 0;
            font-size: .75em;
            grid-column: 1;
        }
        textarea {
            font-family: "space mono", monosans;
            padding: 1em;
            background: var(--grijs);
            border: none;
            color: rgba(255, 255, 255, 0.5);
            grid-column: 2;
            grid-row: 2;
            height: 100%;
        }
        input[type="submit"] {
            margin: 2em 0 1em 0;
            background-color: var(--sec);
            border: none;
            color: rgb(255, 255, 255);
            padding: 1em;
            width: 100%;
            grid-column: 1 / 3;
            cursor: pointer;
            transition: background .25s ease;
            font-weight: 700;
            &:hover { background: #1976d2; }
        }
    }
}

.sectie {
    display: flex;
    flex-direction: column;
    width: 100%;
    opacity: 0;
    transition: all 1s ease;
    input {
        padding: 1em 0 1em 1em;
        background: var(--grijs);
        border: none;
        color: rgba(255, 255, 255, 0.5);
        grid-column: 1;
        font-weight: 700;
    }
    div {
        display: flex;
        flex-direction: column;
        padding-right: 1em;
        span {
            width: 100%;
            background: var(--donkergrijs);
            height: 5px;
            transition: background .5s ease;
        }
    }
}

#about {
    grid-row: 2;
    margin-bottom: 75px;
    height: auto;
    display: grid;
    grid-template-columns: 10vw 1fr 2fr 10vw;
    grid-template-rows: 200px 1fr;
    h2 {
        grid-column: 2 / 4;
        grid-row: 1;
        align-self: center;
        font-size: 3em;
        color: white;
        z-index: 99;
        border-left: 10px solid var(--sec);
        padding-left: 25px;
    }
    p {
        padding-top: 2.5em;
        grid-column: 2;
        line-height: 2em;
        color: white;
        opacity: .7;
        grid-row: 2 / 3;
        grid-column: 2 / 4;
        z-index: 2;
        font-size: 1em;
    }
    a {
        grid-column: 2;
        color: white;
        font-weight: bold;
        text-decoration: none;
        z-index:1;
        transition: color .25s ease;
        border-left: 5px solid var(--sec);
        padding-left: 20px;
        margin-top: 2em;
        &:hover { color: var(--sec); }
    }
}

.square {
    grid-row: 1 / 3;
    grid-column: 3;
    position: sticky;
    width: 40vw;
    height: 50vh;
    right: 10vw;
    overflow: hidden;
    top: 20%;
    justify-self: end;
    z-index: -100;
    img {width:100%;}
}

#work {
    grid-row: 3;
    height: auto;
    display: grid;
    grid-template-columns: 10vw auto 10vw;
}

.work_header {
    grid-row: 1;
    grid-column: 2;
    position: sticky;
    top: -13px;
    z-index: 998;
}

#filter {
    justify-content: space-between;
    cursor: pointer;
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    background: var(--donkergrijs);
    z-index: 999;
    button {
        background: none;
        border: none;
        color: white;
        cursor: pointer;
        border-bottom: 2px solid var(--grijs);
        padding: 1em 2em;
        font-family: 'Space Mono', monospace;
        transition: border-bottom .25s ease;
        &:hover {border-bottom: 2px solid var(--sec);}
    }
}

.nums {
    padding: .25em .5em;
    width: 50px;
    height: 50px;
    color: var(--sec);
    border: 2px solid var(--grijs);
    border-radius: 25px;
    margin-left: 1em;
    font-weight: 900;
    font-size: .75em;
}

.gallerij_fotos {
    column-count: 3;
    column-gap: 2.5em;
    grid-column: 2;
    grid-row: 2;
    padding: 5em 0 5em 0;
    div {
        overflow: hidden;
        span {
            position: absolute;
            margin-top: 20px;
            margin-left: -100px;
            color: white;
            text-shadow: 0px 2px 5px black;
            text-transform: uppercase;
            font-weight: bold;
            opacity: 0;
            transition: opacity .25s ease;
        }
    }
    img {
        width: 100% !important;
        height: auto !important;
        padding-bottom: 2.5em;
        transition: .5s ease;
        transition-property: transform, filter, opacity; 
        transform: translateY(50px);
        opacity: 0;
    }
}

.opacity {opacity: 1;}

.error {background-color: var(--forth)!important;cursor: not-allowed!important;}

.inview {
    transform: translateY(0px)!important;
    opacity: 1!important;
}

.filter {
    filter:grayscale(1);
    opacity: .25!important;
}

.handje {
    transform: rotate(-30deg);
    transform-origin: center;
    position: absolute;
    animation: beweging 2s infinite cubic-bezier(.5,-0.75,.49,1.75);
}

@keyframes beweging {
    0% {transform: rotate(-30deg);}
    50% {transform: rotate(30deg);}
    100% {transform: rotate(-30deg);}
}

.emoji {
    margin-left: 20px;
    position: absolute;
}

.fade {
    position: fixed;
    width: 100%;
    height: 150px;
    background-image: linear-gradient(var(--donkergrijs), transparent);
    z-index: 9;
    pointer-events: none;
}

.informatie {
    grid-row: 2;
    grid-column: 1;
    color: white;
    a {
        color: white;
        text-decoration: none;
        font-weight: 800;
        padding-bottom: .2em;
        margin-top: .5em;
        border-bottom: 2px solid var(--sec);
    }
    h3 {
        font-size: 3em;
        margin-top: .25em;
        margin-bottom: .25em;
    }
    h6 {
        margin-top: 0;
        opacity: .25;
        letter-spacing: .25em;
        font-family: "space mono";
        font-weight: 100;
        font-size: .75em;
    }
    p {
        opacity: .7;
        font-size: .75em;
        line-height: 25px;
        margin: 2em;
    }
}

.project_content {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.project_extra_content {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.extra_info {
    p {margin:0;}
    h5 {margin: 0;color: var(--sec);}
}

.project_links {
    display: flex;
    flex-direction: column;
    align-items: start;
}

.cross {
    z-index: 99;
    color: var(--sec);
    font-size: 2em;
    align-self: center;
    i {
        transition: transform .25s ease;
        &:hover { transform: rotate(90deg) scale(1.25); }
    }
}

.info_block {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
}

.info_header {
    border-bottom: 2px solid rgba(255, 255, 255, 0.25);
    padding-bottom: .5em;
    padding-left: 2em;
}

.arrows {
    display: flex;
    padding: 1em 0;
}

.linker_arrow, .rechter_arrow {
    display: flex;
    padding:.25em 0;
    justify-content: center;
    align-items: center;
    transition: all .25s ease!important;
    text-decoration: none;
    color: white;
    cursor: pointer;
}

.linker_arrow {
    flex-direction: row-reverse;
    &.hover { transform: translateX(-5px); }
    span {
        &:nth-child(1) {
            padding: 0 .25em 0 1em;
            transition: transform .5s ease;	
            color: var(--sec);
        }
        &:nth-child(2) {
            font-size: .75em;
            opacity: .25;
        }
    }
}

.rechter_arrow {
    &.hover { transform: translateX(5px);}
    span {
        &:nth-child(1) {
            padding: 0 1em 0 .25em;
            transition: transform .5s ease;
            color: var(--sec);
        }
        &:nth-child(2) {
            font-size: .75em;
            opacity: .25;
        }
    }
}


.preview {color: white; img {width:100%;}}

.projecten_full {
    top: 0px;
    width: 100%;
    height: 100%;
    grid-row: 2;
    grid-column: 2;
    z-index: 9;
    background: var(--donkergrijs);
    display: none;
}

.showit {display: block;}

@import 'components/_responsive'